import common from '../../common/common.js'
import util from '../../common/util.js'
	
export default {
	data() {
		return {
			baseUrl:common.baseUrl,
			shopid:0,				//店铺ID
			shop:[],				//店铺信息
			catList:[],				//分类列表
			categoryList:[],		//店铺商品类别
			goodsList:[],			//店铺商品列表
			catid:-1,				//当前类别ID
			cindex:0,				//当前key
			canbuy:1,
			scrollLeft:0,
			
			bannerList:[],			//广告列表
			
			cat_active:0,			//默认选中大类
			catfid:0,				//默认大类
			
			menu:{
				mindex:1,
				shopid:0,
				shop:[],
			},
			
			info:{
				bg_color:'0,170,250',
				bg_alpha:1,
				delta:2,
				icon_color:'white'
			},
			titleHeight:0,
			statusBarHeight:0,
			naviBarHeight:0,
			mainTop:0,
			
			mycartList:[],			//购物车
			totalnums:0,			//总数量
			totalamount:0.00,		//总金额
			opened:false,			//是否弹出购物车信息
			
			isall:0,				//是否全部
			page:1,
			
			goods:[],				//当前选择商品
			hideitem:1,
			guige:[],
			guige_name:'',       //选择的规格名称
			guige_key: -1,     //选中的规格键值
			guige_price:0,         //选中规格的价格
			guige_stocks:0,
			nums:1,
			
			
		}
	},
	onLoad(option) {
		var that = this
		var shopid = option.id
		var fid = option.fid
		if(fid > 0){
			that.catfid = fid			
		}
		console.log("catfid----",that.catfid);
		uni.setStorageSync('fid', fid);
		
		//shopid = 2;
		var mycartList = uni.getStorageSync('mycartList');
		var timestamp = Date.parse(new Date());
		var mycartList_expire = uni.getStorageSync('mycartList_expire');
		if( timestamp > mycartList_expire && mycartList){
			//过期
			uni.setStorageSync('mycartList', '');
			uni.setStorageSync('mycartList_expire', '');
		}
		that.mycartList = mycartList != '' ? mycartList : []
		
		// shopid = 10;
		if(shopid > 0){
			that.menu.shopid = shopid
			that.shopid = shopid
			uni.setStorageSync('shopid', shopid);
			//获取店铺信息
			that.getShopInfo();
			//获取店铺分类
			that.getShopCatList();
			//获取店铺广告
			//that.getShopBanner();
			//获取店铺商品类别信息
			//that.getShopCategoryList();
			setTimeout(function(){
				//获取店铺商品信息
				that.getShopGoodsList();
			},1000)
			
		}
		
		
		that.mainTop = common.titleHeight + 40;
	
		
		setTimeout(function(){
			that.updateCartInfo();
		},1000)
		
	},
	onPullDownRefresh() {
		var that = this
		uni.showNavigationBarLoading();
		
		//获取店铺信息
		that.getShopInfo();
		//获取店铺分类
		that.getShopCatList();
		//获取店铺广告
		//that.getShopBanner();
		//获取店铺商品类别信息
		//that.getShopCategoryList();
		//获取店铺商品信息
		that.page = 1;
		that.goodsList = [];
		that.isall = 0;
		setTimeout(function(){
			//获取店铺商品信息
			that.getShopGoodsList();
		},1000)
		
		uni.hideNavigationBarLoading();
		uni.stopPullDownRefresh();
	},
	onShareAppMessage(res) {
		var that = this
		if (res.from === 'button') {// 来自页面内转发按钮
		  console.log(res.target)
		}
		var userInfo = uni.getStorageSync('userInfo');
		var shopid = that.shopid
		
		if(userInfo.users_id > 0){
			var shareUrl = '/pages/merchant/products?id='+shopid+'&fid='+that.catfid+'&tjuid='+userInfo.users_id
		}else{
			var shareUrl = '/pages/merchant/products?id='+shopid+'&fid='+that.catfid
		}
		console.log("shareUrl",shareUrl)
		return {
		  title: that.shop.shop_title,
		  path: shareUrl,
		}
	},
	methods: {
		changeCat(e){
			var that = this
			var index = e.currentTarget.dataset.index
			that.cat_active = index
			that.page = 1;
			that.goodsList = [];
			that.isall = 0;
			that.scrollLeft = index*60
			that.catfid = that.catList[index]['cat_id']
			//获取店铺商品信息
			that.getShopGoodsList();
		},
		
		getShopCatList(){
			var that = this
			var shopid = that.shopid
			if(shopid == '' || shopid == 0){
				return false
			}
			uni.request({
				url: common.apiUrl + 'shop/getShopCatList',
				method: 'GET',
				data: {
					shopid:shopid
				},
				success: res => {			
					var cat_list = res.data.cat_list
					that.catList = cat_list
					if(that.catfid > 0){
						for(var i=0;i<cat_list.length;i++){
							if(cat_list[i]['cat_id'] == that.catfid){
								that.cat_active = i;
								that.scrollLeft = i*60;
								break
							}
						}
					}
				},
				fail: () => {},
				complete: () => {}
			});
		},
		//购物车数量加
		cartNumsADD:function(){
			var that = this
			var nums = that.nums
			var limitnums = that.limitnums
			var stocks = that.guige_stocks
			var guige_key = that.guige_key
			if(guige_key == -1){
				uni.showToast({ title: '请先选择规格',icon: 'loading',duration: 1000 })
				return false
			}
			
			if(limitnums > 0){
			  //有限购
			  if(nums < limitnums){
				if(nums < stocks){
				  //有库存
				  nums++
				  that.nums = nums
				}else{
				  //库存存不足
				  uni.showToast({ title: '库存不足',icon: 'loading',duration: 1000 })
				  return false
				}        
			  }else{
				//超过限购数量
				uni.showToast({ title: '超过限购数量', icon: 'loading', duration: 1000 })
				return false
			  }
			}else{
			  if (nums < stocks) {
				//有库存
				nums++
				that.nums = nums
			  } else {
				//库存存不足
				uni.showToast({ title: '库存不足', icon: 'loading', duration: 1000 })
				return false
			  }
			  
			}
			//console.log(that.data.nums)
		},
		
		//购物车数量减
		cartNumsJIAN: function () {
			var that = this
			var nums = that.nums
			var limitnums = that.limitnums
			var stocks = that.guige_stocks

			if(nums > 1){
			  nums--
			  that.nums =  nums
			}else{
			  return false;
			}
		},
		
		//手动更新数量
		updateCartNums:function(e){
			var that = this
			var nums = parseInt(e.detail.value);
			var limitnums = that.limitnums
			var stocks = that.guige_stocks
			
			if (nums > 0){
			  if (limitnums > 0) {
				//有限购
				if (nums <= limitnums) {
				  if (nums <= stocks) {
					//有库存          
					that.nums =  nums
				  } else {
					//库存存不足
					uni.showToast({ title: '库存不足', icon: 'loading', duration: 1000 })
					that.nums =  stocks
					return false
				  }
				} else {
				  //超过限购数量
				  uni.showToast({ title: '超过限购数量', icon: 'loading', duration: 1000 })
				  that.nums =  limitnums
				  return false
				}
			  } else {
				if (nums <= stocks) {
				  //有库存        
				  that.nums =  nums
				} else {
				  //库存存不足
				  uni.showToast({ title: '库存不足', icon: 'loading', duration: 1000 })
				  return false
				}

			  }
			}else{
			  that.nums =  1
			}
		},
		//选择规格
		  chooseGuige:function(e){
			var that = this
			var key = e.currentTarget.dataset.key
			var guige = e.currentTarget.dataset.guige
			var price = e.currentTarget.dataset.price			
			
			if (key == that.guige_key){
				that.guige_name = ''
				that.guige_key = -1
				that.guige_price = 0
				that.guige_stocks = 0
			}else{
				that.guige_name = guige
				that.guige_key = key
				that.guige_price = price
				that.guige_stocks = that.guige[key][3]
			}
			
		  },
		//关闭弹出层
		  closePopCart:function(){
			var that = this
			that.hideitem = 1;
			that.guige_name = ''
			that.guige_key = -1
			that.guige_price = 0
			that.guige_stocks = 0;
			that.nums = 1;
		  },
		
		  //弹出
		  popCart:function(){
			var that = this
			that.hideitem = 0;
		  },
		  
		  //加入购物车
		  addToCart:function(){
			var that = this
			that.checkAttr();
			if (that.guige_key == -1) {
			  wx.showToast({
				title: '请选择规格',
				icon: 'none',
				duration: 1000
			  })
			  return false
			}
		
		   
		
			var userInfo = uni.getStorageSync("userInfo");
			var id = that.goods.goods_id;
			var title = that.goods.goods_title;
			var nums = that.nums;
			var guige_name = that.guige_name;
			var price = that.guige_price;
		
			
			if (userInfo.users_id > 0) {
			  
			  uni.showToast({
				title: '加载中',
				icon: 'loading',
				duration: 1000
			  })
			  var guige = that.guige
			  var kindex = that.guige_key;
			  var stocks = guige[kindex][3];
			  var mycartList = that.mycartList;
			  var userInfo = uni.getStorageSync('userInfo');
			  
			  
			  if( stocks <= 0 || stocks < nums){
			  	uni.showToast({
			  		title:'库存不足',
			  		duration:1500,
			  		icon:'none'
			  	})
			  	return false
			  }
			  
			  var ishas = 0;
			  console.log("stocks",stocks)
			  for(var i=0;i<mycartList.length;i++){
			  	if(mycartList[i][1] == id && mycartList[i][5] == kindex){
					var tnum = mycartList[i][2]
					var t_nums = tnum +nums 
					console.log("t_nums",t_nums)
			  		if( stocks < t_nums){
			  			uni.showToast({
			  				title:'库存不足',
			  				duration:1500,
			  				icon:'none'
			  			})
			  			return false
			  		}
			  		mycartList[i][2] = mycartList[i][2] + nums;							
			  		ishas = 1;
			  	}
			  }
			  	
			  	console.log("mycartList",mycartList)				
			  if(ishas == 0){
			  	var temparr = [];
			  	temparr.push(that.goods.goods_shopid);		//店铺ID
			  	temparr.push(id);						//商品ID
			  	temparr.push(nums);						//数量
			  	temparr.push(price);					//价格
			  	temparr.push(title);					//标题
			  	temparr.push(kindex);					//规格KEY
			  	temparr.push(guige_name);				//规格名称
			  	console.log(temparr)
			  	mycartList.push(temparr);
			  }
			  console.log(mycartList);
			  
			  that.updateCartInfo();
			  
			  //wx.setStorageSync('mycartList', mycartList);
			  var timestamp = Date.parse(new Date());
			  var expire = timestamp + 3600000
			  uni.setStorageSync('mycartList', mycartList);
			  uni.setStorageSync('mycartList_expire', expire);
			  that.closePopCart();
		
			} else {
			  //app.checkUserLogin();
			  uni.showModal({
				title: '提示',
				showCancel: false,
				content: '亲，您还没有登录，现在去登录',
				success: function (res) { 
				  uni.navigateTo({
					url: '/pages/user/login?tfrom=goods&id='+that.id
				  })
				}
			  })
		
			}
		  },
		
		  //参数验证
		  checkAttr:function(){
			var that = this
		
			if(that.guige_name == ''){
			  uni.showToast({
				title: '请选择规格',
				icon: 'loading',
				duration: 1000
			  })
			  return false
			}else{
			  return true
			}
			
		  
		  },
		//下单,到购物车确认页面
		checkout(){
			var that = this
			var userInfo = uni.getStorageSync('userInfo');
			var mycartList = that.mycartList;
			if(userInfo == ''){
				uni.navigateTo({
					url: '/pages/userlogin/userlogin'
				});
				return false;
			}else{				
				setTimeout(function(){
					uni.navigateTo({
						url: '/pages/cart/cart'
					});
				},1000);
				
				
			}
			
		},
		//操作购物车信息
		opcart(e){
			var that = this
			var target = e.currentTarget.dataset
			var id = target.id;
			var index = target.index;
			var op = target.op;
			var price = target.price;
			var title = target.title;
			var stocks = target.stocks;
			var goodsList = that.goodsList;
			var mycartList = that.mycartList;
			var userInfo = uni.getStorageSync('userInfo');			
			var ishas = 0;
			
			var currentTime = new Date().getTime();
			var tnowtime = currentTime/1000;
			var activity = that.activity
			
			
			if (op == 'addOne'){//添加
				if( stocks <= goodsList[index]['nums'] ){
					uni.showToast({
						title:'库存不足',
						duration:1500,
						icon:'none'
					})
					return false
				}
				mycartList[index][2]++;
			}
			if(op == 'subOne'){
				mycartList[index][2]--
				if(mycartList[index][2] == 0){
					mycartList.splice(index,1);
				}
			}
			
			
			that.updateCartInfo();
			
			that.mycartList = mycartList
			if(mycartList == ''){
				that.opened = false
			}
			
			//wx.setStorageSync('mycartList', mycartList);
			var timestamp = Date.parse(new Date());
			var expire = timestamp + 3600000
			uni.setStorageSync('mycartList', mycartList);
			uni.setStorageSync('mycartList_expire', expire);
			
			//that.getGoodsList();
			
		},
		//加入购物车
		operate(e){
			//wx.setStorageSync('mycartList', '');
			var that = this
			var target = e.currentTarget.dataset
			var id = target.id;
			var sindex = target.sindex;
			var index = target.index;
			var op = target.op;
			var price = target.price;
			var title = target.title;
			var stocks = target.stocks;
			var goodsList = that.goodsList;
			var mycartList = that.mycartList;
			var userInfo = uni.getStorageSync('userInfo');
			
			that.goods = goodsList[index]
			that.guige = goodsList[index]['goods_guige']
			
			
			if( stocks <= 0){
				uni.showToast({
					title:'库存不足',
					duration:1500,
					icon:'none'
				})
				return false
			}
			if(goodsList[index]['goods_guige'] != '' || goodsList[index]['goods_guige'] != ''){
				that.popCart();
				return false;
			}
			
			var ishas = 0;
			
			if (op == 'addOne'){//添加
					console.log("goodsList",goodsList);
					console.log("index",index);				
					
					var t_temai = 0;
					for(var i=0;i<mycartList.length;i++){
						if(mycartList[i][1] == id){
							if( stocks <= goodsList[index]['nums']){
								uni.showToast({
									title:'库存不足',
									duration:1500,
									icon:'none'
								})
								return false
							}
							mycartList[i][2]++;							
							ishas = 1;
							goodsList[index]['nums']++;
						}
					}
						
						console.log("mycartList",mycartList)				
					if(ishas == 0){
						var temparr = [];
						temparr.push(that.shop.shop_id);		//店铺ID
						temparr.push(id);						//商品ID
						temparr.push(1);						//数量
						temparr.push(price);					//价格
						temparr.push(title);					//标题
						goodsList[index]['nums'] = 1;
						console.log(temparr)
						mycartList.push(temparr);
					}
					
					
			}
			if(op == 'subOne'){
				for(var i=0;i<mycartList.length;i++){
					if(mycartList[i][1] == id){
						mycartList[i][2]--;
						ishas = 1;
						goodsList[index]['nums']--;
						if(mycartList[i][2] == 0){
							mycartList.splice(i,1);
							goodsList[index]['nums'] = 0;
						}
					}
				}
			}
			console.log(mycartList);
			
			that.goodsList = goodsList;
			that.updateCartInfo();
			
			//wx.setStorageSync('mycartList', mycartList);
			var timestamp = Date.parse(new Date());
			var expire = timestamp + 3600000
			uni.setStorageSync('mycartList', mycartList);
			uni.setStorageSync('mycartList_expire', expire);
			
		},
		
		//更新购物车信息
		updateCartInfo(){
			var that = this
			var totalamount = 0;
			var totalnums = 0;
			var mycartList = that.mycartList;
			//var activity = that.activity
						
			var all_dis = 0;
			for (let j = 0; j < mycartList.length; j++) {
			  
			  totalnums += mycartList[j][2];
			  //var tt = parseFloat(nums_arr[j]) * parseFloat(price_arr[j]);				 
			  totalamount += mycartList[j][2]*mycartList[j][3];
			  var t_amount = mycartList[j][2]*mycartList[j][3];
			  t_amount = t_amount.toFixed(2)
			}		
			totalamount = Math.round(totalamount * 100) / 100;		
		
			that.totalnums = totalnums;
			that.totalamount = totalamount;
			
		},
		
		//弹出或隐藏购物车信息
		toggleCartInfo(){
			var that = this
			var opened = that.opened;
			that.opened = opened == true ? false : true;
			
		},
		
		//清空购物车
		clearCart(){
			var that = this
			uni.showModal({
				title: '提示',
				content: '确定要清空吗？',
				success: function (res) {
					if (res.confirm) {
						console.log('用户点击确定');
						uni.removeStorageSync('mycartList');
						uni.removeStorageSync('mycartList_expire');
						that.mycartList = [];
						that.opened = false
						that.peisong = false;
						that.totalamount = 0;
						that.totalnums = 0;
						that.getShopGoodsList();
						console.log('aa');
						that.updateCartInfo();
						
					} else if (res.cancel) {
						console.log('用户点击取消');
					}
				}
			});
		},
		
		//联系商家
		contactShop(){
			var that = this
			var shop_mobile = that.shop.shop_mobile
			uni.makePhoneCall({
				phoneNumber:shop_mobile
			})
		},
		//切换分类
		chooseCat(e){
			var that = this
			var catid = e.currentTarget.dataset.catid
			that.catid = catid
			that.page = 1;
			that.goodsList = [];
			that.isall = 0;
			that.getShopGoodsList();
		},
		
		//加载更多商品
		subloadmore(e){
			var that = this				
			var page = that.page
			var isall = that.isall
				
			//1秒内只能加载一次
			var currentTime = new Date().getTime();
			var nowtime = currentTime/1000	
			console.log('nowtime',nowtime)
			console.log('that.scolltime',that.scolltime)
			if(nowtime - that.scolltime < 1){
				return false;
			}
			that.scolltime = nowtime
				
			if(isall == 0){
				page++;
				that.page = page;
				that.getShopGoodsList();
			}
		},
		
		//获取店铺商品信息
		getShopGoodsList(){
			var that = this
			var shopid = that.shopid
			console.log("shopid",shopid)
			if(shopid == '' || shopid == 0){
				return false
			}
			var catfid = that.catfid
			if(catfid == 0){
				catfid = that.catList[that.cat_active]['cat_id']
			}
			console.log("catfid",catfid)
			uni.request({
				url: common.apiUrl + 'shop/getShopGoodsList',
				method: 'GET',
				data: {
					catfid:catfid,
					catid:that.catid,
					shopid:shopid,
					page:that.page,
				},
				success: res => {
					var goodsList = res.data.goods_list
					var mycartList = that.mycartList
					var total_page = res.data.total_page;
					if(that.page == total_page){
						that.isall = 1;
					}
					for(var i=0;i<goodsList.length;i++){
						var t_nums = 0;
						for(var j=0;j<mycartList.length;j++){
							if(mycartList[j][1] == goodsList[i]['goods_id']){
								t_nums = mycartList[j][2]
							}
						}
						goodsList[i].nums = t_nums;
						that.goodsList.push(goodsList[i])
					}
					that.canbuy = res.data.canbuy
				},
				fail: () => {},
				complete: () => {}
			});
		},
		
